// 感谢你使用此组件，觉得不错的话去github点个Star吧~
// https://github.com/gameruleCEO/sib-list
export default {
	name: 'sib-loading',
	props: {
		isTop: {
			type: [Number, String],
			default: 1
		},
		loadText: {
			type: [String, String],
			default: '松开刷新'
		},
		scrollHeight: {
			type: [String, String],
			default: '500px'
		},
		isNoList: {
			type: [Boolean, String],
			default: false
		},
		noListText: {
			type: [String, String],
			default: '暂无数据...'
		},
		floterText: {
			type: [String, String],
			default: '数据加载中...'
		},
		isUseTop: {
			type: [Boolean, String],
			default: true
		},
		isGtHeight: {
			type: [Number, String],
			default: 1000
		}
	},
	data() {
		return {
			isTranf: 0,
			touchStart: '',
			touchMove: '',
			isEnd: 0,
			isShowToTop: false,
			topView: ''
		};
	},

	methods: {
		// 刷新开始
		refreshStart: function(e) {
			if (this.isEnd == 0 && this.isTop == 1) {
				this.touchStart = e.changedTouches[0].pageY
			}
		},
		// 刷新移动
		refreshMove: function(e) {
			if (this.isEnd == 0 && this.isTop == 1) {
				var touchStart = this.touchStart,
					oldMove = this.touchMove,
					newMove = e.changedTouches[0].pageY
				if (touchStart <= newMove) {
					var isTranf = touchStart > newMove ? 0 : newMove - touchStart
					this.isTranf = isTranf
					this.touchMove = e.changedTouches[0].pageY
				}
			} else {
				this.isTranf = 0
				this.isEnd = 0
				this.touchStart = 9999
			}
		},
		// 刷新结束
		refreshEnd: function(e) {
			var that = this
			if (this.isEnd == 0 && this.isTop == 1) {
				if (this.isTranf >= 90) {
					this.isTranf = 125
					this.isEnd = 1
					this.$emit('isRefresh');
				} else {
					this.isTranf = 0
				}
			}
		},
		// 结束整个刷新过程
		endAfter: function() {
			this.isEnd = 2
			setTimeout(() => {
				this.isTranf = 0
				this.isEnd = 0
			}, 600)
		},
		// 触底
		scrolltolower: function(e) {
			this.$emit('scrolltolowerFn');
		},
		// 回到顶部
		toTopClick: function() {
			this.topView = ''
			setTimeout(() => {
				this.topView = 'topId'
			}, 10)
		},
		// 移动
		scroll: function(e) {
			if(!this.isUseTop) {
				return
			}
			if (e.detail.scrollTop > this.isGtHeight) {
				this.isShowToTop = true
			} else {
				this.isShowToTop = false
			}
		}
	},
	computed: {
		isTranform: function() {
			var isTranf = this.isTranf > 150 ? 150 : this.isTranf
			var isTemp = `transform: translateY(${isTranf- 60}px);`
			return isTemp
		},
		isZoom: function() {
			var isTranf = this.isTranf > 125 ? 125 : this.isTranf
			var isTemp = `zoom:${isTranf/125};`
			return isTemp
		}
	}

}
